@import "colors";

.home {
  width: 100%;
  height: 100vh;
  background-color: $second;
  border-radius: 0 0 200px 200px;

  > main {
    position: absolute;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 100%;
    height: 100%;
    padding: 2rem;
    justify-content: center;

    h1 {
      font: 400 6rem $text1;
      color: $first;

    }
    p {
      font: 900 1rem $text1;
      color: $first;
    }

    .profile-pic {
      position: absolute;
      top: 100px;
      left: 100px;
      width: 500px; // Made the image larger
      height: 500px; // Made the image larger
      border-radius: 70%;
      object-fit: contain;
      border: 2px solid $first;
    }
  }
    &::before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        background: no-repeat center/auto url("../assets/1.png");
        filter: blur(3px);
        border-radius: 0 0 200px 200px;
      }
    }
.home2{
  width: 100%;
  height: 100vh;
  background-color: $second;
  padding: 500px  0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -200px;

  > img{
    width: 30%;
    animation: imgUp  0.7s linear infinite alternate;
  }
  
  @keyframes imgUp {
    to{
      transform: translateY(-20px);
    }
  }
  > div{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5rem;
    height: 100vh;

  }
  p{
    color: $first;
    letter-spacing: 2px;
    word-spacing: 5px;
    font: 100 1.2rem $text2;
  }
}
  
  .home3{
  width: 100%;
  height: 150vh;
  background-color: $second;
  margin-top: -200px;
  display: flex;
  justify-content: flex-end;
  > div{
  width: 70%;
  height: 100vh;
  background-color: $first;
  border-radius: 200px 0 0 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 15rem;


  > h1{
    text-transform: uppercase;
    border-bottom: 1px solid $dark;
    padding: 1rem;
    width:  14rem;
    color: $dark;

   }

  > p{
    padding: 0.1rem;
    letter-spacing: 2px;
    word-spacing: 5px;
    line-height: 170%;
    font: italic 100 1.2rem $text2;

  }
 }
  }

  .skills{
    width: 100%;
  height: 120vh;
  background-color: $second;
  margin-top: -200px;
  display: flex;
  > div{
  width: 70%;
  height: 100vh;
  background-color: $first;
  border-radius: 0 200px 200px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 15rem;


  > h1{
    text-transform: uppercase;
    border-bottom: 1px solid $dark;
    padding: 1rem;
    width:  14rem;
    color: $dark;

   }
   > ul {
    padding: 1rem;
    list-style-type: disc;
    margin: 1rem 0 0 1.5rem;

    li {
      margin-bottom: 0.5rem;
      color: $dark;
      font: italic 100 1.2rem $text1;
    }
  }
    }
   }
  

.home4{
  width: 100%;
  height: 100vh;
  background-color: $second;
  margin-top: -100px;
  display: flex;
  justify-content: flex-end;

  > div{
    background-color: $third;
    border-radius: 200px 0 0 200px;
    width: 50%;
    height: 60vh;
    padding: 10rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    > h1{
      color: $first;
      text-transform: uppercase;
      width: 140px;
      border-bottom: 2px solid $first;
      padding: 0.5rem;
    }

    > article{
      display: flex;
      margin: 2rem 0;
      flex-wrap: wrap;
      justify-content: center;
      > div{
        animation: imgUp 0.7s linear infinite alternate;
        margin: 1rem;
        height: 6rem;
        width: 6rem;
        background-color: $first;
        border-radius: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: $dark; cursor: pointer;
        transition: all 0.5s;
        &:hover{
          filter: invert(1);

        }
        svg{
          font-size: 4rem;

        }
        p{
          font: 900 1,2rem $text2;

        }

      }

    }

  }
}